/**
 * Checks parameters to see if we should use FormData to send the request
 * @param params The object whose keys will be encoded.
 * @return A boolean indicating if FormData will be required.
 */
export function requiresFormData(params) {
  return Object.keys(params).some(function (key) {
    var value = params[key];

    if (!value) {
      return false;
    }

    if (value && value.toParam) {
      value = value.toParam();
    }

    var type = value.constructor.name;

    switch (type) {
      case "Array":
        return false;

      case "Object":
        return false;

      case "Date":
        return false;

      case "Function":
        return false;

      case "Boolean":
        return false;

      case "String":
        return false;

      case "Number":
        return false;

      default:
        return true;
    }
  });
}
/**
 * Converts parameters to the proper representation to send to the ArcGIS REST API.
 * @param params The object whose keys will be encoded.
 * @return A new object with properly encoded values.
 */

export function processParams(params) {
  var newParams = {};
  Object.keys(params).forEach(function (key) {
    var param = params[key];

    if (param && param.toParam) {
      param = param.toParam();
    }

    if (!param && param !== 0 && typeof param !== "boolean" && typeof param !== "string") {
      return;
    }

    var type = param.constructor.name;
    var value; // properly encodes objects, arrays and dates for arcgis.com and other services.
    // ported from https://github.com/Esri/esri-leaflet/blob/master/src/Request.js#L22-L30
    // also see https://github.com/Esri/arcgis-rest-js/issues/18:
    // null, undefined, function are excluded. If you want to send an empty key you need to send an empty string "".

    switch (type) {
      case "Array":
        // Based on the first element of the array, classify array as an array of objects to be stringified
        // or an array of non-objects to be comma-separated
        value = param[0] && param[0].constructor && param[0].constructor.name === "Object" ? JSON.stringify(param) : param.join(",");
        break;

      case "Object":
        value = JSON.stringify(param);
        break;

      case "Date":
        value = param.valueOf();
        break;

      case "Function":
        value = null;
        break;

      case "Boolean":
        value = param + "";
        break;

      default:
        value = param;
        break;
    }

    if (value || value === 0 || typeof value === "string") {
      newParams[key] = value;
    }
  });
  return newParams;
}